import React, { Component } from "react";
import styled from "styled-components";
import url from "./img/url.png";

import "./App.css";
import { AiFillHeart } from "react-icons/ai";
import { MdSkipNext, MdSkipPrevious, MdPlayCircleFilled } from "react-icons/md";

const Main = styled.div`
  width: 450px;
  height: 80vh;
  border-radius: 15px;

  display: flex;
  flex-direction: column;
  background-color: #71888f5e;

  @media (max-width: 375px) {
    width: 100%;
    height: 80vh;

    img {
      width: 50%;
    }
  }


  img {
    max-width: 400px;
    min-width: 300px;

    height: auto;
    padding: 5%;
    margin: 0 auto;
  }

  h6 {
    font-weight: 500;
    font-size:15px;

  }
  p {
    font-weight: 300;
    font-size: 18px;
  }
  #play {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 50px;
  }
`;


export class Spotify extends Component {

  
  render() {
    const {name,track,image,color}=this.props.state
    return (
      <div>
        <Main>
          <img src={image} alt="pp" className="img-spotify"/>
          <div 
            style={{
              display: "flex",
              justifyContent: "start",
              padding: "0 30px",
            }}
          >
            <div style={{ flex: "1", textAlign: "left",color:color}}>
              <h6>{track}</h6>
              <p>{name} </p>
            </div>
            <AiFillHeart size="30px" color={this.props.state.color}/>
          </div>
          {/*    <FaRandom/>*/}
          <div id="play">
            <MdSkipPrevious  color={this.props.state.color}/>
            <MdPlayCircleFilled color={this.props.state.color}/>
            <MdSkipNext color={this.props.state.color}/>
          </div>
          <img src={url} alt="pp" />
         
        </Main>
      </div>
    );
  }
}

export default Spotify;
